.wave{
    background-image: url(data:image/png;base64,);
    height: 200px;
    width: 100%;
    background-repeat: no-repeat;
    background-size: 100% 250px;
    box-shadow: 0px 10px 0px 0px rgba(238,226,212,1);
}

.waverotate{
    height: 200px;
    background-image: url('./../../../photo/wave.png');
    background-repeat: no-repeat;
    background-size: 100% 250px;
    transform: rotate(180deg);
    box-shadow: 0px 10px 0px 0px rgba(238,226,212,1);
}